#list {
  background-color: rgb(248, 248, 248);
  border-radius: 0.3rem;
  border: 2px solid rgb(127, 197, 255);
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;
}

ol {
  list-style-position: inside;
  padding-left: 0;
}

ol > p {
  font-weight: bold;
  text-align: center;
}

#icon {
  font-size: 3em;
  margin: 0;
  padding: 0;
}

li::marker {
  color: rgb(113, 144, 184);
  font-weight: bold;
}

li > span {
  padding-left: 0.5em;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
  padding: 1em;
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 6px solid rgb(255, 255, 255);
  border-color: rgb(99, 99, 99) transparent rgb(99, 99, 99) transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
